<template>
  <scroll  id="tab-menu">
    <div>
      <div class="menu-list">
        <div
          class="menu-list-item"
          :class="{active: index===currentIndex}"
          v-for="(item, index) in categories"
          :key="index.id"
          @click="itemClick(index)"
        >{{item.title}}</div>
      </div>
    </div>
  </scroll>
</template>
<script>
import Scroll from "components/common/scroll/Scroll";
export default {
  name: "TabMenu",
  components: {
    Scroll
  },
  props: {
    categories: Array
  },
  data() {
    return {
      currentIndex: 0
    };
  },
  created() {},
  methods: {
    itemClick(index) {
      this.currentIndex = index;
      this.$emit("selectItem", index);
    }
  }
};
</script>
<style scoped>
#tab-menu {
  background-color: #f6f6f6;
  height: 100%;
  width: 100px;
  box-sizing: border-box;
}

.menu-list-item {
  height: 45px;
  line-height: 45px;
  text-align: center;
  font-size: 14px;
}

.menu-list-item.active {
  font-weight: 700;
  color: var(--color-high-text);
  background-color: #fff;
  border-left: 3px solid var(--color-high-text);
}
</style>
